.container__footer {
  max-width: 1440px;
  width: 100%;
  height: 70px;
  padding: 0 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (max-width: 768px) {
    display: none;
  }
  .footer__links{
    display: flex;
    justify-content: space-between;
    .links__link{
      position: relative;
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: var(--dark-grey);
      margin-right: 13px;

    }
  }
}